* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html,
body {
  overflow-x: hidden;
  cursor: none;
  height: 100dvh;
  width: 100vw;
  background-color: rgb(21, 25, 30);
  font-size: 10px;
  font-family: "Poppins", sans-serif;
  color: #ffffff;
}

body {
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
}
@media all and (max-width: 1000px) {
  body {
    scroll-snap-type: none;
    overflow-y: auto;
  }
}

a,
h1,
h2,
h3,
h4,
h5,
h6,
p,
div,
span,
li,
ul,
button,
input {
  cursor: none;
  color: white;
}

p {
  letter-spacing: 1.2;
}

#preloader {
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: 1000;
  background-color: rgb(21, 25, 30);
  transition: all 5s linear;
}
#preloader .overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#preloader .overlay .preloaderSVGContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding: 1%;
  animation: fadeOut 5s linear forwards 4s;
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  65% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
#preloader .overlay .preloaderSVGContainer .preloaderSVG {
  padding: 0%;
  min-height: 200px;
  min-width: 80vw;
  position: absolute;
  scale: 1;
  stroke: white;
  filter: drop-shadow(0px 0px 10px rgba(0, 150, 158, 0.25));
  stroke-dasharray: 150;
  stroke-dashoffset: 150;
  animation: dash 2s infinite alternate;
}
@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
  from {
    stroke-dashoffset: 150;
  }
}
#preloader .preload-finish {
  opacity: 0;
  transition: all 4s ease-in-out;
}

.gsapCursor {
  z-index: 10000;
  width: 15px;
  height: 15px;
  position: fixed;
  top: 0;
  left: 0;
  border-radius: 50%;
  pointer-events: none;
  transition: width 0.4s ease-in-out, height 0.4s ease-in-out;
  overflow: visible;
  background-color: white;
  filter: drop-shadow(0px 0px 4px rgba(188, 188, 188, 0.25));
  width: 25px;
  height: 25px;
  mix-blend-mode: difference;
}
@media only screen and (max-width: 700px) {
  .gsapCursor {
    display: none;
  }
}

