:root {
  --marquee-height: 10vh;
}

.marquee {
  align-self: center;
  display: flex;
  z-index: 300;
  width: 100%;
  height: 30vh;
  color: #eee;
  overflow: hidden;
  position: relative;
}
.marquee .marquee-content {
  list-style: none;
  height: 100%;
  display: flex;
  position: relative;
  justify-content: space-evenly;
}
.marquee .marquee-content:hover, .marquee .marquee-content:active {
  animation-play-state: paused;
}
.marquee .marquee-content span {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-shrink: 0;
  height: 100%;
  font-size: calc(var(--marquee-height) * 2.5 / 4);
  filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.5));
}
.marquee .marquee-content span img {
  width: 100%;
}
.marquee .fab {
  position: relative;
  scale: 1;
}
.marquee .fa-js {
  opacity: 0;
  animation: pulseJS 3.35s infinite alternate ease-in-out 10ms;
  scale: 1;
}
@keyframes pulseJS {
  from {
    opacity: 0;
    transform: scale(0.45);
    filter: blur(1px);
  }
  to {
    opacity: 1;
    transform: scale(0.65);
    filter: blur(0);
  }
}
.marquee .fa-sass {
  opacity: 0;
  animation: pulseSass 3.41s infinite alternate ease-in-out 900ms;
  scale: 1;
}
@keyframes pulseSass {
  from {
    opacity: 0;
    transform: scale(0.45);
    filter: blur(1px);
  }
  to {
    opacity: 1;
    transform: scale(0.65);
    filter: blur(0);
  }
}
.marquee .fa-git {
  opacity: 0;
  animation: pulseGit 3.34s infinite alternate ease-in-out 92ms;
  scale: 1;
}
@keyframes pulseGit {
  from {
    opacity: 0;
    transform: scale(0.45);
    filter: blur(1px);
  }
