:root {
  --marquee3-width: 100vw;
  --marquee3-height: 8vh;
  --marquee3-elements-displayed: 1.75;
  --marquee3-element-width: calc(
    var(--marquee3-width) / var(--marquee3-elements-displayed)
  );
  --marquee3-animation-duration: calc(var(--marquee3-elements) * 20s);
}
@media all and (min-width: 1100px) {
  :root {
    --marquee3-elements-displayed: 3;
  }
}

.marquee3 {
  width: var(--marquee3-width);
  height: var(--marquee3-height);
  color: #eee;
  overflow: hidden;
  position: relative;
}

.marqueeCTA {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.marquee-content3 {
  list-style: none;
  height: 100%;
  display: flex;
  animation: scrolling3 var(--marquee3-animation-duration) linear infinite;
}
.marquee-content3:hover {
  animation-play-state: paused;
}

@keyframes scrolling3 {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-1 * var(--marquee3-element-width) * var(--marquee3-elements)));
  }
}
.marquee-content3 li {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  top: 4px;
  flex-shrink: 0;
  text-transform: uppercase;
  width: var(--marquee3-element-width);
  max-height: 100%;
  white-space: nowrap;
  font-weight: 600;
  font-family: "Raleway", sans-serif;
}

.marquee-content3 span {
  color: #00adb5;
}

@media (max-width: 600px) {
  :root {
    --marquee3-width: 100vw;
    --marquee3-height: 8vh;
    --marquee3-elements-displayed: 1.55;
  }
}
#footerWrapper {
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 8vh;
  width: 100%;
}
#footerWrapper .footerSpan {
  margin: 0 30px;
  filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25));
  font-size: 0.85rem;
  display: flex;
  text-align: center;
}